.title {
    min-height: 5vh;
    background-color: rosybrown;
    padding: 0;
    margin: 0;
    width: 100%;
    text-align: center;
} 

.text {
    padding: 0;
    margin: 0;
    padding-top: 1vh;
    text-align: center;
    font-family: 'Poppins',sans-serif;
    font-size: 1em;
    color: #e0e5eb;
    text-shadow: 
    -8px -8px 12px rgba(255, 255, 255, 0.4),
    8px 8px 12px rgba(0, 0, 0, 0.8);
    caret-color: #262626;
}

a {
    padding: 0;
    margin: 0;
    border: 0;
    text-decoration: none;
    color: #e0e5eb;
} 